import {
  Component,
  Input,
  Output,
  EventEmitter,
  OnChanges
} from "@angular/core";
import { PagerService } from "./pagination.service";
@Component({
  selector: "app-pagination",
  templateUrl: "./pagination.component.html",
  styleUrls: ["./pagination.component.scss"]
})
export class PaginationComponent implements OnChanges {
  constructor(private pagerService: PagerService) {}

  // array of all items to be paged
  @Input() allItems = [];
  @Output() changePage = new EventEmitter<any>();
  @Input() customPageSize: any;
  // pager object
  pager: any = {};

  // paged items
  pagedItems = [];

  ngOnChanges() {
    this.setPage(1);
  }

  setPage(page: number) {
    if (this.allItems.length > 0) {
      if (page < 1 || page > this.pager.totalPages) {
        return;
      }
      let page_size = 10;
      if (this.customPageSize) {
        page_size = this.customPageSize;
      } else {
        const config_manage =
          JSON.parse(localStorage.getItem("config_manage")) || [];
        const data = config_manage.find(o => o.param === "PAGE_SIZE");
        page_size = data ? +data.value : 10;
        page_size = page_size <= 0 ? 10 : page_size;
      }
      // get pager object from service
      this.pager = this.pagerService.getPager(
        this.allItems.length,
        page,
        page_size
      );

      // get current page of items
      this.pagedItems = this.allItems.slice(
        this.pager.startIndex,
        this.pager.endIndex + 1
      );
      this.changePage.emit(this.pagedItems);
      return;
    }
    this.changePage.emit([]);
  }
}
