import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { DashboardComponent } from "./dashboard/dashboard.component";
import { TradingTopupComponent } from "./trading-topup/trading-topup.component";
import { OrderComponent } from "./order/order.component";

export const routes: Routes = [
  {
    path: "service",
    children: [
      {
        path: "",
        component: DashboardComponent,
      },
      {
        path: "trading-topup",
        component: TradingTopupComponent,
        data: {
          title: "Nạp tiền điện thoại",
          urls: [
            { title: "Dịch vụ tiện ích", url: "/service" },
            { title: "Nạp tiền điện thoại" },
          ],
        },
      },
      {
        path: "order",
        component: OrderComponent,
        data: {
          title: "Mua mã thẻ điện thoại",
          urls: [
            { title: "Dịch vụ tiện ích", url: "/service" },
            { title: "Mua mã thẻ điện thoại" },
          ],
        },
      },

      {
        path: "recharge",
        loadChildren: () =>
          import("./recharge/recharge.module").then((m) => m.RechargeModule),
      },
    ],

    // canActivate: [AuthGuard, AuthGuardAgency],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ShopRoutingModule {}
