import { Component, OnInit } from "@angular/core";
import { formatAmount } from "../../shared/constants/constant";

@Component({
  selector: "app-order",
  templateUrl: "./order.component.html",
  styleUrls: ["./order.component.scss"],
})
export class OrderComponent implements OnInit {
  formatAmount = formatAmount;

  amounts = [];
  vendors = [];
  nearestOrders = [];

  selectedVendor = null;
  currentOrder = {};

  total = 0;
  discountTotal = 0;
  errorMessage = null;
  isConfirm = true;

  constructor() {}

  ngOnInit(): void {}

  onSubmit() {
    this.isConfirm = true;
  }

  onSelectVendor(item) {}

  onSelectAmount($event) {}

  handleCallBack(event) {
    // console.log(event);
    if (event.cmd === "go-back") {
      this.isConfirm = false;
    }
  }
}
