import { Component, OnInit, Input, Output, EventEmitter } from "@angular/core";
import { formatAmount } from "../../../shared/constants/constant";

@Component({
  selector: "app-trading-topup-confirm",
  templateUrl: "./trading-topup-confirm.component.html",
  styleUrls: ["./trading-topup-confirm.component.scss"],
})
export class TradingTopupConfirmComponent implements OnInit {
  @Input() currentTopup: any;
  @Output() callBack = new EventEmitter<any>();
  formatAmount = formatAmount;

  confirmPassword = "";
  isSuccess = true;
  constructor() {}

  ngOnInit(): void {}

  onReset() {}

  onConfirm() {
    this.isSuccess = true;
  }

  onGoBack() {
    this.callBack.emit({
      cmd: "go-back",
    });
  }
}
